
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.error {
  margin: 10;
  padding: 5;
  color: rgb(85, 187, 85);
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}



.item1 { grid-area: header; }
.item2 { grid-area: list; }
.item3 { grid-area: main; }
.item4 { grid-area: right; }
.item5 { grid-area: footer; }

.grid-container {
  display: grid;
  grid-template-areas:
    'header header header header header header'
    'list main main main right right'
    'menu footer footer footer footer footer';
  grid-gap: 10px;
  background-color: rgb(149, 241, 206);
  padding: 10px;
}
.grid-container > div {
  background-color: rgba(213, 143, 245, 0.925);
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
}



